.hompage{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 80px;
}
    .menu{
       display: flex;
       width: 100%; 
       flex-wrap: wrap;
       justify-content: space-between;
    }

.directory{
    display: flex;
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;

    &:first-child{
        margin-right: 7.5px;
    }

    &:last-child{
        margin-left: 7.5px;
    }

    .items{
        height: 90px;
        margin: 0 25px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border: 1px solid black;

        .title{
            margin-bottom:6px ;
            color: #4a4a4a;
            font-size: 2rem;
            font-weight: bold;
        }

        .sub-title {
            font-size: 1.5rem;
            font-weight: lighter;
            margin-bottom:2.5rem ;

        }
    }
}